@import "./../../Variable.scss";

.profileThree-container {
	flex-basis: 576px;
	margin-top: 1.5rem;


	.tab {
		overflow: hidden;
		// background-color: black;
		display: flex;
		justify-content: space-between;

		button {
			background-color: inherit;
			float: left;
			border: none;
			outline: none;
			cursor: pointer;
			// padding: 14px 0px;
			transition: 0.3s;
			font-size: 17px;
			// font-weight: 600;
			margin: 0 1rem;
			color: #7c7c7c;
			border-bottom: 2px solid transparent;

			font-family: 'Roboto', sans-serif;

			&:hover {
				color: black;
				font-weight: 600;
				border-bottom: 2px solid #1da1f2;
			}

			&:active {
				color: black;
				font-weight: 600;
				border-bottom: 2px solid #1da1f2;
			}
		}
	}

	.content-div {

		.tabcontent {
			display: flex;
			justify-content: center;
			text-align: center;
			border-top: none;
		}
	}
}

.photo-grid-center {
	display: inline-flex;
	justify-content: center;
}

.photo-grid {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 20px;
}
