.change-password-main-container {
    width: 100%;
    height: calc(100vh - 50px);
    transition: 0.8s ease-in-out;
}
.login-input-new{
    transition: 0.8s ease-in-out;
}
.option-back-div {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0 1rem;
}

.changePasswordOverlay {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #388e3c;
    color: white;
    padding: 10px;
    border-radius: 5px;
    text-align: center;
    z-index: 1000;
    width: 100%;
    max-width: 300px;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    font-weight: 600;
}

.change-password-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 567px;
    padding: 0 1rem;
}

.error-message {
    background-color: #cc2e2e;
    border-radius: 5px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 18px;
    font-weight: 600;

}